/***************************************************/
/* BoardList */


/* header */
.header{
  /* padding: 20px 0; */
  text-align: center;
  width: 500px;
  height: 30px;
  margin: 30px auto;
  
  display: flex;
  justify-content: space-between;
}
  
/* 메인 검색창 */
.header input{
  width: 300px;
  margin: 0 10px;
}

/* 메인 버튼 */
.header button{
  width: 80px;
  font-size: 14px;
  font-weight: bold;
  background-color: #455ba8;
  border: none;
  color: white;
  cursor: pointer;
}

.header  button:hover{
  background-color: white;
  color : #455ba8;
  border: 2px solid #455ba8;
}

/* 메인 목록 */
.list{
  padding: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 메인 목록 정보 */
.list .content{
  flex-basis: 100%;
}

.list p{
  padding-top: 5px;
}


/* 메인 목록 버튼영역 */
.list .board-btn-area{
  flex-basis: 15%;
}

/* 메인 목록 버튼 */
.list .board-btn-area > button{
  width: 80px;
  height: 30px;

  font-size: 15px;
  font-weight: bold;
  border: 0;
  background-color: white;
  color : #455ba8;
  border: 2px solid #455ba8;
  cursor: pointer;
}

*::selection {
  background-color: transparent; /* 선택된 영역의 배경색을 투명하게 설정 */
  color: inherit; /* 선택된 텍스트의 색상을 부모 요소의 색상으로 설정 */
}

.content > h4 > span{
  cursor: pointer;
}

/* 모달 */
.modal{
  height: 200px;
  margin-top : 20px;
  padding : 20px;
  background : #ccc;
  text-align : left;
}

/* 시크릿 링크 */
.secretlink{
  color: white;
  cursor: default;
}



/* BoardDetail */

.detailMain{

  border: 2px solid gainsboro;
  margin-top: 50px;
}
.detailBtn{
  display: flex;
  justify-content: right;
}

.detailBtn button{
  margin-left:  10px;

  width: 80px;
  height: 30px;

  font-size: 15px;
  font-weight: bold;
  border: 0;
  background-color: #455ba8;
  color : white;
  border: 2px solid #455ba8;
  cursor: pointer;
}

.detail-error{
  display: flex;
  height: 600px;
  justify-content: center;
  align-items: center;
  color: red;
  font-weight: bold;
  font-size: 30px;
}

.detail-header {
  display: flex;
  justify-content: center;
}

.board-info{
  margin-left: auto;
}


.board-info div {
  margin-bottom: 10px; /* 각각의 작성일과 수정일 사이의 간격을 조정합니다. */
}

.board-info p {
  margin: 0;
  display: inline-block; /* Make spans inline-block to allow horizontal arrangement */
  margin-right: 10px; /* Add some space between spans */
}

.board-info p span {
  margin-right: 5px; /* span 태그 사이 간격을 조정합니다. */
}




.board-writer span {
  display: inline-block; /* Make spans inline-block to allow horizontal arrangement */
  margin-right: 10px; /* Add some space between spans */
  
}


.board-editing {
  margin-bottom: 20px;
}

.board-editing input{
  width: 100%;
  padding: 10px;
  font-size: 25px;
  border: none;
  margin-top: 20px;

}



.board-editing textarea {
  width: 100%;
  height: 200px; /* 원하는 높이로 설정하세요 */
  padding: 10px;
  font-size: 16px;
  border: none;
  resize: none; /* textarea의 크기 조절 비활성화 */
}
  

.board-editing hr {
  border: none;
  border-top: 1px solid #ccc;
  margin: 20px 0;
}